*,
body,
html {
    font-family: 'Roboto', sans-serif;
    -webkit-box-sizing: border-box;
            box-sizing: border-box
}

header {
    background: #0c002b;
    color: white;
    overflow: auto;
    padding: 20px 15px
}

header .logo {
    float: left
}

header nav {
    float: right
}

header nav a {
    color: white;
    text-decoration: none;
    padding: 10px 20px;
    text-transform: capitalize;
    font-weight: 500;
    font-size: .9rem
}

header nav a:hover {
    color: #ff718e
}

header nav a.register-btn {
    background: #ff718e;
    padding: 7px 25px;
    text-align: center;
    color: white;
    border-radius: 5px
}

#content-area {
    overflow: auto;
    width: 100%;
    min-height: 100vh
}

#filter {
    background: #FAF7f5;
    width: 30%;
    max-width: 300px;
    display: inline-block;
    float: left
}

#filter .inside {
    padding: 20px;
    max-width: 300px
}

#filter h4 {
    font-size: 0.9rem;
    margin-bottom: 25px;
    font-weight: 500px;
    text-transform: uppercase
}

#filter select {
    background: transparent;
    padding: 10px;
    font-size: 14px;
    font-weight: 500;
    color: #1b1b1b;
    display: block;
    margin-bottom: 25px;
    width: 100%
}

#filter .filters .title {
    font-size: 0.9rem;
    display: block;
    margin-bottom: 1rem;
    color: #ada8aa
}

#filter .filters input[type="text"] {
    border-radius: 5px;
    font-size: 1rem;
    padding: 0.4rem;
    width: 45%;
    display: inline-block;
    margin-right: 2.5%;
    margin-bottom: 1rem
}

#filter .filters.extras label {
    display: block;
    width: 100%;
    padding: 10px 0
}

#filter .filters.extras label input[type="checkbox"] {
    float: right
}

#listings {
    width: 70%;
    float: left;
    display: inline-block;
    padding: 15px;
    background: #fffeff
}

#listings .search-area input[type="text"] {
    width: 70%;
    background: transparent;
    font-size: 2rem;
    border: none;
    border-bottom: 1px solid #ada8aa;
    margin-bottom: 20px;
    font-weight: 100;
    color: #d1cbcd
}

#listings .sortby-area {
    margin-bottom: 20px
}

#listings .sortby-area .results {
    color: #ada8aa;
    font-size: 0.8rem;
    display: inline-block
}

#listings .sortby-area .sort-options {
    float: right
}

#listings .sortby-area .sort-options select {
    font-size: 0.9rem;
    font-weight: 300;
    border: 1px solid #d1cbcd;
    background: transparent;
    padding: 1px;
    display: inline-block;
    margin-right: 15px;
    vertical-align: top
}

#listings .sortby-area .sort-options .view {
    display: inline-block
}

#listings .sortby-area .sort-options .view .fa {
    margin-right: 15px;
    font-size: 1.5rem
}

#listings .listings-results {
    margin-bottom: 60px
}

#listings .listings-results .listing {
    background: #fff;
    border-radius: 5px;
    -webkit-box-shadow: 0 9px 54px -4px #e0e0e0;
            box-shadow: 0 9px 54px -4px #e0e0e0
}

#listings .listings-results .listing-img {
    color: white;
    padding: 15px;
    position: relative;
    background: url("http://cdn.freshome.com/wp-content/uploads/2014/04/Gorgeous-evening-view-of-Russet-Residence--1025x450.jpg") no-repeat center center;
    background-size: cover;
    min-height: 200px;
    -webkit-border-top-left-radius: 5px;
    -webkit-border-top-right-radius: 5px;
    -moz-border-radius-topleft: 5px;
    -moz-border-radius-topright: 5px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px
}

#listings .listings-results .listing-img:hover .details {
    opacity: 1
}

#listings .listings-results .listing-img:hover .address {
    opacity: 0
}

#listings .listings-results .listing-img .address {
    font-size: 1.3rem;
    text-transform: capitalize;
    position: absolute;
    bottom: 20px;
    opacity: 1;
    -webkit-transition: all 0.4s ease-in-out;
    -o-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
    text-shadow: 0 1px 4px #5b5b5b
}

#listings .listings-results .listing-img .details {
    -webkit-transition: all 0.4s ease-in-out;
    -o-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
    opacity: 0;
    padding: 30px;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    -webkit-border-top-left-radius: 5px;
    -webkit-border-top-right-radius: 5px;
    -moz-border-radius-topleft: 5px;
    -moz-border-radius-topright: 5px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    background: rgba(78, 2, 229, 0.8);
    background: -webkit-linear-gradient(315deg, rgba(78, 2, 229, 0.8) 0%, rgba(157, 28, 237, 0.8) 100%);
    background: -o-linear-gradient(315deg, rgba(78, 2, 229, 0.8) 0%, rgba(157, 28, 237, 0.8) 100%);
    background: linear-gradient(135deg, rgba(78, 2, 229, 0.8) 0%, rgba(157, 28, 237, 0.8) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#4e02e5', endColorstr='#9d1ced', GradientType=1)
}

#listings .listings-results .listing-img .details .user-img {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    border: 2px solid white;
    display: inline-block;
    background: url("http://cdn.freshome.com/wp-content/uploads/2014/04/Gorgeous-evening-view-of-Russet-Residence--1025x450.jpg") no-repeat center center;
    background-size: cover
}

#listings .listings-results .listing-img .details .user-details {
    vertical-align: top;
    display: inline-block
}

#listings .listings-results .listing-img .details .user-details .user-name {
    font-weight: 300;
    font-size: 0.9rem;
    display: block;
    color: #fff;
    margin-top: 3px;
    margin-bottom: 5px
}

#listings .listings-results .listing-img .details .user-details .post-date {
    font-size: 0.9rem;
    display: block;
    color: rgba(255, 255, 255, 0.7)
}

#listings .listings-results .listing-img .details .listing-details {
    margin-top: 20px;
    margin-bottom: 10px
}

#listings .listings-results .listing-img .details .listing-details .fa {
    display: inline-block;
    font-size: 20px;
    margin-right: 15px
}

#listings .listings-results .listing-img .details .listing-details span {
    font-weight: 300;
    font-size: 0.8rem;
    display: inline-block;
    vertical-align: top;
    position: relative;
    top: 3px
}

#listings .listings-results .listing-img .details .listing-details .floor-space {
    margin-bottom: 10px
}

#listings .listings-results .bottom-info {
    padding: 20px 15px
}

#listings .listings-results .bottom-info .price {
    font-size: 1rem;
    display: block;
    margin-bottom: 10px;
    color: #ada8aa
}

#listings .listings-results .bottom-info .location {
    font-size: 1.2rem;
    display: block;
    color: #d1cbcd
}

.view-btn {
    background: black;
    padding: 8px;
    font-size: 0.9rem;
    border-radius: 5px;
    text-align: center;
    max-width: 150px;
    -webkit-transition: background 0.3s ease-in-out;
    -o-transition: background 0.3s ease-in-out;
    transition: background 0.3s ease-in-out
}

.view-btn:hover {
    background: #ff718e;
    cursor: pointer
}

#pagination {
    width: 100%;
    display: block;
    text-align: center
}

#pagination .pages {
    margin: 0 auto;
    display: inline-block;
    background: white;
    -webkit-box-shadow: 0 9px 54px -4px #e0e0e0;
            box-shadow: 0 9px 54px -4px #e0e0e0
}

#pagination .pages li {
    padding: 20px 30px;
    display: inline-block
}

#pagination .pages li.active,
#pagination .pages li:hover {
    color: white;
    font-weight: 700;
    background: #ff718e;
    cursor: pointer
}


